import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            title: '编辑',
            cOn: [],
            all: false,
            list: [],
            user: this.$store.state.userInfo.Data,
        }
    },
    created(){
        this.pull()
    },
    mounted(){
        this.$refs.back.zIndex = false
    },
    methods: {
        pull(){
            this.$post('IMAPI/Account/GetOftenUseWordsList',{
                UserId: this.user.User.DoctorId,
                UserType: 2
            }).then(o => {
                this.list = o
            })
        },
        getAll(){
            if(this.all){
                this.list.forEach(v => {
                    this.cOn.push(v.Id)
                })
            }
            else{
                this.cOn = []
            }
        },
        pop(){
            if(this.cOn.length){
                this.$post('IMAPI/Account/DelOftenUseWords',JSON.stringify(this.cOn)).then(o => {
                    this.pull()
                })
            }
            else{
                this.$toast('请选择要删除的快捷回复')
            }
        },
        toMod(i){
            this.$store.dispatch('pushreply',i)
            this.$router.push('/add-reply')
        },
    }
}



